.containerHeadInf,
.containerHeadInfBegin {
  /* background-color: rgba(158, 249, 255, 0.75); */
  background-color: #fff;
  min-width: 50vw;
  min-height: 50vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 20px 30px;
  align-items: center;
  border-radius: 20px;
  position: relative;
  gap: 20px;
  max-width: 75vw;
  position: relative;
}
.containerHeadInfBegin {
  background-color: transparent;
}
.containerHeadInfBegin {
  padding: 0;
}
.input {
  min-width: 20px;
  min-height: 20px;
  display: inline-block;
  opacity: 0.7;
}
.input:disabled {
  position: absolute;
  display: none;
}
label {
  display: flex;
  align-items: center;
  gap: 0.5vw;
}
label h3 {
  font-weight: 400;
}
label span {
  display: none;
  justify-content: center;
  align-items: center;
  color: white;
  font-weight: 900;
}
label .input:disabled + span {
  display: flex;
  background: rgb(175, 167, 167);
  color: rgb(175, 167, 167);
  opacity: 0.7;
  cursor: pointer;
  min-width: 20px;
  height: 20px;
}
label .input:checked + span {
  background: #003d8a;
  color: white;
}

.patientHeaderInfo {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  margin-bottom: 100px;
}
.containerHeadInf h2,
.patientHeaderInfo h2,
.containerHeadInfBegin h2 {
  color: #003d8a;
  text-align: center;
}
.patientHeaderInfo img {
  max-width: 250px;
}
.patientHeaderInfo p {
  max-width: 500px;
  text-align: center;
  font-weight: bold;
  font-size: 20px;
}
.containerHeadInfBegin {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.footerButtons {
  display: flex;
  justify-content: space-between;
  gap: 10px;
  width: 100%;
}
.firstVisitInfo {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 10px;
}
.informationBlock {
  padding: 30px 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 10px;
  border-radius: 20px;
  cursor: pointer;
  transition: 0.5s;
}
.informationBlock:hover {
  transform: scale(1.05);
}
.currentVariants {
  display: grid;
  grid-template-columns: max-content max-content;
  grid-column-gap: 30px;
  grid-row-gap: 10px;
}
.currentVariants h3 {
  max-width: 550px;
}

.summaryBlock {
  max-width: 600px;
  display: flex;
  flex-direction: column;
  gap: 50px;
}
.summaryBlock ul {
  margin-left: 20px;
}
.summaryBlock h3 {
  margin-right: auto;
}

.finish {
  font-size: calc(var(--index));
  margin: auto 0;
}

@media (max-width: 1700px) {
  .currentVariants h3 {
    max-width: 400px;
  }
}
@media (max-width: 1200px) {
  .currentVariants h3 {
    max-width: 350px;
  }
}

@media (max-width: 1150px) {
  .currentVariants {
    grid-template-columns: max-content;
  }
  .footerButtons {
    display: grid;
    grid-template-areas: "a b" "c c";
    gap: 10px;
  }
}

@media (max-width: 980px) {
  .firstVisitInfo {
    grid-template-columns: 1fr 1fr;
  }
}
@media (max-width: 720px) {
  .currentVariants {
    grid-template-columns: 350px;
  }
  .informationBlockn:hover {
    transform: none;
  }
  .finish {
    font-size: calc(var(--index) * 2.5);
    text-align: center;
  }
}
@media (max-width: 550px) {
  .containerHeadInf,
  .containerHeadInfBegin {
    padding: 20px 10px;
    gap: 15px;
    max-width: max-content;
  }
  .currentVariants h3 {
    font-size: 16px;
  }
  .informationBlock {
    padding: 5px 10px;
  }
  .informationBlock div {
    font-size: 14px;
  }
  .footerButtons {
    margin-top: 5vh;
  }
}
@media (max-width: 400px) {
  .currentVariants {
    grid-template-columns: 300px;
  }
  .currentVariants h3 {
    max-width: 310px;
  }
}
