.App {
  display: flex;
  min-height: 100vh;
  align-items: center;
  justify-content: center;
  /* background-image: url("./Images/fone.png"); */
  background-color: #023f86;
  background-position: center;
  background-size: cover;
  padding: 20px 5px;
}
body::-webkit-scrollbar {
  width: 5px;
}
body::-webkit-scrollbar-thumb {
  /* плашка-бегунок */
  background: red;
  border-radius: 10px;
}
body::-webkit-scrollbar-track {
  /* фон */
  background: black;
}

:root {
  --index: 1vw + 1vh;
}

.buttonNext,
.buttonBack {
  border: none;
  padding: 10px 25px;
  font-size: 18px;
  background-color: rgb(211, 162, 0);
  box-shadow: 1px 1px 5px black;
  border-radius: 15px;
  color: white;
  cursor: pointer;
}
.buttonBack {
  background-color: #004390a1;
  grid-area: a;
}
.buttonNext {
  grid-area: b;
}
.modalBack{
  background-color: rgb(211, 162, 0);
  position: fixed;
  left: 0;
  top: 0;
}
.commentary-button {
  background-color: #003e8a75;
  grid-area: c;
}
img {
  max-width: 100%;
  opacity: 0.9;
}
.password-style {
  width: 30vw;
  height: 5vh;
  font-size: 18px;
  padding: 2px 1vw;
}

@media (max-width: 1000px) {
  .password-style {
    width: 50vw;
    height: 6vh;
  }
}
@media (max-width: 500px) {
  .password-style {
    width: 80vw;
    height: 7vh;
  }
}
